}

@-webkit-keyframes slide-out-container{
    0%{
        height: 100vh;
    }
    40%{
        height: 100vh;
    }
    100%{
        height: 0vh;
    }
}

@keyframes slide-out-container{
    0%{
        height: 100vh;
    }
    40%{
        height: 100vh;
    }
    100%{
        height: 0vh;
    }
}
.awak{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
}

.header{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    font-size: 100px;
    z-index: -1;
    color:rgb(245, 7, 7);

}
.header p{
    font-size: 20px;
    font-weight: 100;
    color: gray;
}

.container{
    width: 100%;
    height: 100vh;
    margin: 0,auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #161616;
    -webkit-animation: slide-out-container 4s cubic-bezier(0.97,0.01,0.36,0.99)2.8s;
            animation: slide-out-container 4s cubic-bezier(0.97,0.01,0.36,0.99)2.8s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

}
.text-wrapper{
    color: white;
    position: absolute;

}
.text{
    font-family: 'Roboto',sans-serif;
    font-weight: lighter;
    font-size: 54px;

}
.text-1, .text-3, .text-4, .text-8, .text-9, .text-11{
    /* color: rgba(0, 0, 0, 0); */
    -webkit-text-stroke:1px white ;
}

.text-1{
    
    animation: blink 0.8s linear 0.9s, blink 0.8s linear 0.2s !important;
    -webkit-animation: blink 0.8s linear 0.9s, blink 0.8s linear 0.2s;


-webkit-text-stroke:1px rgb(0, 38, 255) !important;
}

.text-2 {
    
    -webkit-animation: blink 0.8s linear 0.8s, blink 0.8s linear 2.1s !important;
    
            animation: blink 0.8s linear 0.8s, blink 0.8s linear 2.1s !important;
   
  
    color: rgb(6, 140, 250);
}

